import { Mark, Data } from '@ali/4ever-cangjie';

export interface ShdData extends Data {
  val?: string;
  color?: string;
  fill?: string;
}

interface Shd extends Mark<ShdData> {}

const Shd = {
  TYPE: 'shd',

  create(data: ShdData): Shd {
    return Mark.create<ShdData>({
      type: Shd.TYPE,
      data,
    });
  },

  isShd(maybeShd: any): maybeShd is Shd {
    return Mark.isMark(maybeShd) && maybeShd.type === Shd.TYPE;
  },
};

export default Shd;
